import store from '@/common/common.js'
export const upload = (fileAmount, callback) => {

	// uni.showToast({
	// 	title: '',
	// 	duration: 2000
	// })
	// return;

	uni.chooseImage({
		count: fileAmount, //9
		sizeType: ['compressed'], //
		success: function(res) {
			// res.tempFilePaths 
			console.log("-----",JSON.stringify(res.tempFilePaths));
			let imgs = [];
			res.tempFilePaths.forEach(item => {
				uni.uploadFile({
					url: `${store.apiUrl}/jsp/api/grem/doUploadFile.jsp`,
					name: 'file',
					filePath: item,
					formData: {
						'fileSingleSizeLimit': 10 * 1024 * 1024,
						'fileSizeLimit': '10MB',
						'originalName': "ceshi.jpg",
						'token': `TwfyW3Xe1tblRSns91567764332116`
					},
					success: (res) => {
						let {
							data
						} = JSON.parse(res.data)
						console.log('', JSON.parse(res.data))
						callback(data[0].relativePath)
					},
					fail: (err) => {
						uni.showToast({
							title: err.errMsg,
							icon: 'none'
						})
					}
				});
			})
		}
	});
}
