/**  */
const isNotBlank = (value) => {
  return !isBlank(value);
}


/** loading */
const showLoading = (value) =>{
	if(isBlank(value)){
		uni.showLoading({
    		title: ''
		});
	}else{
		uni.showLoading({
    		title: value
		});
	}
}

